
.browsing-history {
    max-width: calc(100% - 640px);
    min-width: 1200px;
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
}

.sort-history {
    display: flex;
    gap: 16px;
}

.sort-history p {
    display: inline-block;
    margin: 0;
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 16px;
    color: #F1F1F1;
    cursor: pointer;
}

.sort-history .active {
    color: #6D8EEB;
    text-decoration: underline; 
}

.history-list {
    width: 100%;
    margin-top: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    animation: showMovieCards 2s ease-in-out forwards;
}

@keyframes showMovieCards {
    from {
        opacity: 0;
        right: -50px;
    }

    to {
        opacity: 1;
        right: 0;
    }
}

.film-card {
    width: 100%;
    height: auto;
    border-radius: 16px;
    text-decoration: none;
}

.film-poster {
    position: relative;
    border-radius: 16px;
    width: 100%;
    height: 290px;
    transition: 0.5s ease;
}

.film-card:hover .film-poster {
    box-shadow: #6D8EEB 0px 0px 6px;
}

.film-poster img {
    width: 100%;
    height: 100%;
    border-radius: 16px;
    object-position: center;
}

.question-mark {
    opacity: 0;
    position: absolute;
    top: 10px;
    left: 10px;
    width: 30px;
    height: 30px;
    font-size: 14px;
    background-color: #6D8EEB;
    border-radius: 50%;
    font-family: 'Rubik';
    color: #F1F1F1;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.5s ease;
}

.film-card:hover .question-mark {
    opacity: 1
}

.film-info {
    display: none;
    position: absolute;
    top: 0;
    left: 56px;
    width: 500px;
    background-color: #514F5A;
    color: #F1F1F1;
    border-radius: 20px;
    padding: 26px 40px;
    z-index: 3;
    animation: showFilmInfo 0.4s ease-in-out forwards;
}

@keyframes showFilmInfo {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.question-mark:hover+.film-info {
    display: block;
}

.name-rate {
    display: flex;
    justify-content: space-between;
}

.name-rate svg {
    margin-right: 6px;
}

.info-title {
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    margin-bottom: 7px;
}

.info-rate {
    font-family: 'Rubik';
    color: #FBBC05;
    font-size: 14px;
    font-weight: 400;
}

.info p {
    font-family: 'Rubik';
    font-size: 14px;
    margin: 3px 0;
    color: #C9C8DA;
    font-weight: 400;
}

.info-line {
    width: 100%;
    height: 1px;
    background-color: #F1F1F1;
    margin: 10px 0;
}

.info-description h1 {
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    margin-bottom: 6px;
}

.info-description p {
    margin: 0;
    font-family: 'Rubik';
    font-size: 16px;
    font-weight: 400;
    color: #C9C8DA;
}

.quality {
    opacity: 0;
    position: absolute;
    top: 40px;
    right: 0;
    width: 80px;
    height: 28px;
    background-color: #514F5A;
    color: #F1F1F1;
    border-radius: 10px 0 0 10px;
    font-family: 'Rubik';
    padding: 6px 20px;
    font-size: 14px;
    font-weight: 400;
    transition: opacity 0.5s ease;
}

.film-card:hover .quality {
    opacity: 1
}

.film-title {
    width: 100%;
    height: 50px;
    color: #F1F1F1;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    overflow: hidden;
    padding: 12px 5px 6px 5px;
    transition: 0.5s ease;
}

.film-card:hover .film-title {
    text-shadow: #6D8EEB 0px 0px 6px;
}

.none-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 70px;
}

.none-found img {
    width: 600px;
    height: 480px;
}

.none-found p {
    margin: 0;
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 16px;
    color: #FFFFFF;
}